.formControl.error input,
.formControl.error textarea {
  border: 1px solid red;
}

.error span {
  color: red;
}

.formControl input {
  width: 200px;
  height: 30px;
  outline: none;
  border: none;
  border-bottom: solid 1px #ffffff;
  background-color: black;
  color: rgb(59, 145, 135);
}

.formControl textarea {
  width: 200px;
  height: 50px;
  outline: none;
  border: none;
  border-bottom: solid 1px #ffffff;
  background-color: black;
  color: rgb(59, 145, 135);
}

.formControlsWall input {
  height: 30px;
  outline: none;
  border: none;
  border-bottom: solid 1px #ffffff;
  background-color: black;
  color: rgb(59, 145, 135);
}
