.item__project {
  font-size: 60px;
}

.project__block {
  display: grid;
  grid-template:
    minmax(auto, 100px) minmax(auto, 250px) minmax(auto, 100px) / 1fr minmax(
      auto,
      300px
    )
    minmax(auto, 300px) minmax(auto, 300px) 1fr;

  font-size: 18px;
  margin-left: 20px;
  margin-top: 10px;
}

.project__title {
  font-size: 34px;
  grid-column: 2/5;
  grid-row: 1/2;
  padding-left: 10px;

  text-align: center;
  font-family: "CENTURY GOTHIC";

  text-shadow: 6px 0px 12px rgb(34, 34, 34);
}

.project__title_underline {
  background-image: linear-gradient(
    to right,
    rgb(59, 145, 135),
    rgb(255, 255, 255)
  );
  height: 3px;
  width: auto;
  margin-top: 10px;
}

.project__item_one {
  grid-column: 2/3;
  grid-row: 2/3;

  padding: 20px;
  text-align: center;
}

.project__item_two {
  grid-column: 3/4;
  grid-row: 2/3;

  padding: 20px;
  text-align: center;
}

.project__item_three {
  grid-column: 4/5;
  grid-row: 2/3;

  padding: 20px;
  text-align: center;
}

.project__description_one {
  grid-column: 2/3;
  grid-row: 3/4;
  text-align: center;
}

.project__description_two {
  grid-column: 3/4;
  grid-row: 3/4;
  text-align: center;
}

.project__description_three {
  grid-column: 4/5;
  grid-row: 3/4;
  text-align: center;
}

.project__item_one img,
.project__item_two img,
.project__item_three img {
  width: 180px;
  height: 230px;
  box-shadow: 0px 0px 8px 1px white;
}

.project__item_one img:hover,
.project__item_two img:hover,
.project__item_three img:hover {
  transform: scale(1.15);
}