.header {
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1000px) minmax(auto, 400px) 1fr;
  text-align: right;
  margin: 20px 20px 0px 0px;
}

.header__items {
  grid-column: 3/4;
  font-size: 18px;
}

.header__item {
  margin: 0px 60px;
}

a.header__item_activeLink {
  color: turquoise;
}

.header__item:hover {
  color: turquoise;
}
